@import url(https://fonts.googleapis.com/css?family=Montserrat:400);
// Base styles
.sb_list {
    width: 400px;
    height: 600px;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    border: 1px solid #999;
}

.sb_item {
    position: relative;
    border-bottom: 1px solid #999;
}

// Stylized
.sb_stylizedList {
    position: relative;
    z-index: 0;
    background-color: #F3F3F3;
    border: 1px solid #EFEFEF;
    border-radius: 3px;
    outline: none;
}
.sb_stylizedItem {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 0 20px;
    background-color: #FFF;
    border-bottom: 1px solid #EFEFEF;
    box-sizing: border-box;
    user-select: none;

    color: #333;
    font-weight: 400;

    .img {
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        position: absolute;
    }
}

// Drag handle
.sb_handle {
    display: block;
    width: 18px;
    height: 18px;
    background-image: url('data:image/svg+xml;charset=utf-8,<svg xmlns="http://www.w3.org/2000/svg" width="50" height="50" viewBox="0 0 50 50"><path d="M 0 7.5 L 0 12.5 L 50 12.5 L 50 7.5 L 0 7.5 z M 0 22.5 L 0 27.5 L 50 27.5 L 50 22.5 L 0 22.5 z M 0 37.5 L 0 42.5 L 50 42.5 L 50 37.5 L 0 37.5 z" color="#000"></path></svg>');
    background-size: contain;
    background-repeat: no-repeat;
    opacity: 0.25;
    margin-right: 20px;
    cursor: row-resize;
}

// Grid
.sb_grid {
    display: block;
    width: 130 * 4px;
    height: 350px;
    white-space: nowrap;
    border: 0;
    background-color: transparent;
}

.sb_gridItem {
    float: left;
    width: 130px;
    padding: 8px;
    background: transparent;
    border: 0;

    .sb_wrapper {
        display: flex;
        align-items: center;
        justify-content: center;

        width: 100%;
        height: 100%;
        background: #FFF;
        border: 1px solid #EFEFEF;

        font-size: 28px;

        span {
            display: none;
        }
    }
}

.sb_stylizedHelper {
    box-shadow: 0 5px 5px -5px rgba(0,0,0,0.2), 0 -5px 5px -5px rgba(0,0,0,0.2);
    background-color: rgba(255,255,255,0.8);
    cursor: row-resize;

    &.sb_horizontalItem {
        cursor: col-resize;
    }
    &.sb_gridItem {
        background-color: transparent;
        white-space: nowrap;
        box-shadow: none;

        .sb_wrapper {
            background-color: rgba(255,255,255,0.8);
            box-shadow: 0 0 7px rgba(0,0,0,0.15)
        }
    }
}

.sb_shrinkedHelper {
    height: 20px !important;
}



